import { PageBuilder } from './pageBuilder';
import { ColorList, FormsList, objectEntries, Sizes, ValueFilterData } from '../interfaces/types';
import { filterColorsEnum, findValByKey } from '../interfaces/supportConstants';

export class ValueFilterSection extends PageBuilder {
  valueFilter: ValueFilterData;
  updateTrigger: () => void;
  sectionHTML: HTMLElement;
  constructor(valueFilter: ValueFilterData, updateTrigger: () => void) {
    super();
    this.valueFilter = valueFilter;
    this.updateTrigger = updateTrigger;
    this.sectionHTML = this.setElement({ tag: 'SECTION', classList: ['value-filter'] });
  }
  update() {
    const toyformPropertyPairs: objectEntries<boolean> = Object.entries(this.valueFilter.choosedForms);
    const colorsPropertyPairs: objectEntries<boolean> = Object.entries(this.valueFilter.choosedColors);
    const sizesPropertyPairs: objectEntries<boolean> = Object.entries(this.valueFilter.choosedSizes);
    const valueFilterTitle: HTMLElement = this.setElement({
      tag: 'H2',
      classList: ['value-title'],
      innerText: 'фильтры по значению',
    });
    const valueFilterList: HTMLElement = this.setElement({ tag: 'UL', classList: ['value-list'] });
    const formContainer: HTMLElement = this.setElement({ tag: 'LI', classList: ['value-item'] });
    const formTitle: HTMLElement = this.setElement({ tag: 'H3', classList: ['toyform-subtitle'], innerText: 'Форма:' });
    const formList: HTMLElement = this.setElement({ tag: 'UL', classList: ['toyform-list'] });
    const formItems: Array<HTMLElement> = new Array(Object.keys(this.valueFilter.choosedForms).length)
      .fill(null)
      .map((_, index) => {
        const formItem: HTMLElement = this.setElement({ tag: 'LI', classList: ['toyform-item'] });
        const currentKey: string = toyformPropertyPairs[index][0];
        if (this.valueFilter.choosedForms[currentKey as keyof FormsList]) formItem.classList.add('active');
        formItem.setAttribute('style', `background-image: url(../../assets/svg/${currentKey}.svg);`);
        formItem.onclick = (): void => {
          const currentValue: boolean = this.valueFilter.choosedForms[currentKey as keyof FormsList];
          this.valueFilter.choosedForms[currentKey as keyof FormsList] = !currentValue;
          formItem.classList.toggle('active');
          this.updateTrigger();
        };
        return formItem;
      });
    formContainer.append(formTitle, formList);
    formList.append(...formItems);
    const colorContainer: HTMLElement = this.setElement({ tag: 'LI', classList: ['value-item'] });
    const colorTitle: HTMLElement = this.setElement({ tag: 'H3', classList: ['color-subtitle'], innerText: 'Цвет:' });
    const colorList: HTMLElement = this.setElement({ tag: 'UL', classList: ['color-list'] });
    const colorItems: Array<HTMLElement> = new Array(Object.keys(this.valueFilter.choosedColors).length)
      .fill(null)
      .map((_, index) => {
        const currentKey: string = colorsPropertyPairs[index][0];
        const colorItem: HTMLElement = this.setElement({
          tag: 'LI',
          classList: ['color-item'],
          attributes: [
            { name: 'style', value: `background-color:${findValByKey(Object.entries(filterColorsEnum), currentKey)}` },
          ],
        });
        if (this.valueFilter.choosedColors[currentKey as keyof ColorList]) colorItem.classList.add('active');
        colorItem.onclick = (): void => {
          const currentValue: boolean = this.valueFilter.choosedColors[currentKey as keyof ColorList];
          this.valueFilter.choosedColors[currentKey as keyof ColorList] = !currentValue;
          colorItem.classList.toggle('active');
          this.updateTrigger();
        };
        return colorItem;
      });
    colorContainer.append(colorTitle, colorList);
    colorList.append(...colorItems);
    const sizes: Array<string> = ['big', 'medium', 'small'];
    const sizeContainer: HTMLElement = this.setElement({ tag: 'LI', classList: ['value-item'] });
    const sizeTitle: HTMLElement = this.setElement({ tag: 'H3', classList: ['size-subtitle'], innerText: 'Размер:' });
    const sizeList: HTMLElement = this.setElement({ tag: 'UL', classList: ['size-list'] });
    const sizeItems: Array<HTMLElement> = new Array(Object.keys(this.valueFilter.choosedSizes).length)
      .fill(null)
      .map((_, index) => {
        const sizeItem: HTMLElement = this.setElement({ tag: 'LI', classList: ['size-item', sizes[index]] });
        const currentKey: string = sizesPropertyPairs[index][0];
        if (this.valueFilter.choosedSizes[currentKey as keyof Sizes]) sizeItem.classList.add('active');
        sizeItem.onclick = (): void => {
          const currentValue: boolean = this.valueFilter.choosedSizes[currentKey as keyof Sizes];
          this.valueFilter.choosedSizes[currentKey as keyof Sizes] = !currentValue;
          sizeItem.classList.toggle('active');
          this.updateTrigger();
        };
        return sizeItem;
      });
    sizeContainer.append(sizeTitle, sizeList);
    sizeList.append(...sizeItems);
    const favoritesContainer: HTMLElement = this.setElement({ tag: 'LI', classList: ['value-item'] });
    const favoritesSubtitle: HTMLElement = this.setElement({
      tag: 'H3',
      classList: ['favorites-subtitle'],
      innerText: 'Только любимые',
    });
    const favoritesCheckbox: HTMLElement = this.setElement({ tag: 'DIV', classList: ['favorites-checkbox'] });
    if (this.valueFilter.onlyFavorites) favoritesCheckbox.classList.add('active');
    favoritesCheckbox.onclick = (): void => {
      const currentValue: boolean = this.valueFilter.onlyFavorites;
      this.valueFilter.onlyFavorites = !currentValue;
      favoritesCheckbox.classList.toggle('active');
      this.updateTrigger();
    };
    favoritesContainer.append(favoritesSubtitle, favoritesCheckbox);
    valueFilterList.append(formContainer, colorContainer, sizeContainer, favoritesContainer);
    this.sectionHTML.innerHTML = '';
    this.sectionHTML.append(valueFilterTitle, valueFilterList);
  }
}
