import defaultSettings from '../defaultSettings';
import { ToysSettings } from '../interfaces/interfaces';
import { sortAttributesEnum } from '../interfaces/supportConstants';
import { objectEntries } from '../interfaces/types';
import { PageBuilder } from './pageBuilder';

export class SortSection extends PageBuilder {
  parentSettings: ToysSettings;
  updateTrigger: () => void;
  resetTrigger: () => void;
  constructor(parentSettings: ToysSettings, updateTrigger: () => void, resetTrigger: () => void) {
    super();
    this.parentSettings = parentSettings;
    this.updateTrigger = updateTrigger;
    this.resetTrigger = resetTrigger;
  }
  build(): HTMLElement {
    const sortingKeyValPairs: objectEntries<string> = Object.entries(sortAttributesEnum);
    const sortSection: HTMLElement = this.setElement({ tag: 'SECTION', classList: ['sort-section'] });
    const sortTitle: HTMLElement = this.setElement({ tag: 'H2', classList: ['sort-title'], innerText: 'Сортировка:' });
    const sortContainer: HTMLElement = this.setElement({ tag: 'DIV', classList: ['sort-container'] });
    const sortList: HTMLElement = this.setElement({
      tag: 'SELECT',
      classList: ['sort-select'],
      attributes: [{ name: 'name', value: 'sort' }],
    });
    sortList.onchange = (e: Event): void => {
      this.parentSettings.choosedSortKey = (<HTMLSelectElement>e.target).value;
      this.updateTrigger();
    };
    const sortItems: Array<HTMLElement> = new Array(sortingKeyValPairs.length).fill(0).map((_, index) => {
      const optionElement = this.setElement({
        tag: 'OPTION',
        classList: ['sort-option'],
        innerText: sortingKeyValPairs[index][1],
        attributes: [{ name: 'value', value: sortingKeyValPairs[index][0] }],
      });
      if (sortingKeyValPairs[index][0] === this.parentSettings.choosedSortKey)
        optionElement.setAttribute('selected', 'selected');
      return optionElement;
    });
    const resetButton: HTMLElement = this.setElement({
      tag: 'BUTTON',
      classList: ['reset-button'],
      innerText: 'Сброс фильтров',
    });
    resetButton.onclick = () => {
      this.resetTrigger();
    };
    sortSection.append(sortTitle, sortContainer);
    sortContainer.append(sortList, resetButton);
    sortList.append(...sortItems);
    return sortSection;
  }
}
