import { Elementdata } from '../interfaces/interfaces';

export class PageBuilder {
  constructor() {}
  setElement({ tag, classList = undefined, innerText = undefined, attributes = undefined }: Elementdata) {
    const elem = document.createElement(tag) as HTMLElement;
    if (classList) elem.classList.add(...classList);
    if (innerText) elem.textContent = String(innerText);
    if (attributes) attributes.forEach((attr) => elem.setAttribute(attr.name, attr.value));
    return elem;
  }
}
