import './style.scss';
import { App } from './scripts/app';
import { ToyItem } from './scripts/interfaces/interfaces';

fetch('https://raw.githubusercontent.com/cptShell/christmas-data/main/data.json')
  .then((res) => res.json())
  .then((data: Array<ToyItem>) => {
    const app = new App(data);
    app.init();
  });
