import { ToyItem, ToysPageInterface, ToysSettings } from '../interfaces/interfaces';
import { ValueFilterSection } from './valueFilter';
import { RangeFilterSection } from './rangeFilter';
import { ToysSection } from './toysSection';
import { SortSection } from './sortSection';
import { PageBuilder } from './pageBuilder';
import { ColorList, FormsList, Sizes } from '../interfaces/types';

export class ToysPage extends PageBuilder implements ToysPageInterface {
  public toysList: Array<ToyItem>;
  public toysSettings: ToysSettings;
  public valueFilterSection: ValueFilterSection;
  public rangeFilterSection: RangeFilterSection;
  public sortSection: SortSection;
  public toysSection: ToysSection;
  searchValue: string;
  choosedToysDisplay: HTMLElement;

  constructor(toysList: Array<ToyItem>, toysSettings: ToysSettings) {
    super();
    this.toysSettings = toysSettings;
    this.searchValue = '';
    this.valueFilterSection = new ValueFilterSection(this.toysSettings.valueFilters, this.updateToys.bind(this));
    this.rangeFilterSection = new RangeFilterSection(this.toysSettings.rangeFilters, this.updateToys.bind(this));
    this.sortSection = new SortSection(this.toysSettings, this.updateToys.bind(this), this.resetSettings.bind(this));
    this.toysSection = new ToysSection(toysList, this.toysSettings.choosedIdList, this.updateChoosedIdList.bind(this));
    this.choosedToysDisplay = this.setElement({ tag: 'DIV', classList: ['search-found'] });
  }
  public get selectedToysCount(): string {
    return String(this.toysSettings.choosedIdList.length);
  }
  draw(): void {
    const header: HTMLElement = this.setElement({ tag: 'HEADER', classList: ['header'] });
    const navList: HTMLElement = this.setElement({ tag: 'UL', classList: ['nav-list'] });
    const navItems: Array<HTMLElement> = [undefined, 'игрушки', 'ёлка'].map((value, index) => {
      const li: HTMLElement = this.setElement({ tag: 'LI', classList: ['nav-item'] });
      const classList: Array<string> = !index ? ['nav-button', 'icon'] : ['nav-button'];
      const button: HTMLElement = this.setElement({
        tag: 'BUTTON',
        classList: classList,
        innerText: value,
      });
      li.append(button);
      return li;
    });
    const searchContainer: HTMLElement = this.setElement({ tag: 'DIV', classList: ['search-container'] });
    const resetInputButton: HTMLElement = this.setElement({ tag: 'BUTTON', classList: ['reset-input-button'] });
    const searchInputWrapper: HTMLElement = this.setElement({ tag: 'DIV', classList: ['search-input-wrapper'] });
    const searchInput: HTMLElement = this.setElement({
      tag: 'INPUT',
      classList: ['search-input'],
      innerText: null,
      attributes: [
        { name: 'type', value: 'text' },
        { name: 'placeholder', value: 'Поиск' },
      ],
    });
    searchInput.oninput = (e: Event) => {
      this.searchValue = (<HTMLInputElement>e.target).value;
      this.updateToys();
    };
    resetInputButton.onclick = () => {
      (<HTMLInputElement>searchInput).value = '';
      this.searchValue = '';
      this.updateToys();
    };
    this.updateChoosedIdList();
    header.append(navList, searchContainer);
    navList.append(...navItems);
    searchContainer.append(resetInputButton, searchInputWrapper, this.choosedToysDisplay);
    searchInputWrapper.append(searchInput);

    const toysMain: HTMLElement = this.setElement({ tag: 'MAIN', classList: ['toys-section'] });

    const footer: HTMLElement = this.setElement({ tag: 'FOOTER', classList: ['footer'] });
    const rssLogo: HTMLElement = this.setElement({
      tag: 'A',
      classList: ['rss-logo'],
      attributes: [{ name: 'href', value: 'https://rs.school/' }],
    });
    const authorContainer: HTMLElement = this.setElement({ tag: 'DIV', classList: ['author-container'] });
    const spanAuthor: HTMLElement = this.setElement({ tag: 'SPAN', innerText: 'App Developer: IX/CptShell' });
    const spanYear: HTMLElement = this.setElement({ tag: 'SPAN', innerText: '2021' });
    footer.append(rssLogo, authorContainer);
    authorContainer.append(spanAuthor, spanYear);

    this.valueFilterSection.update();
    this.rangeFilterSection.update();
    const sortSection: HTMLElement = this.sortSection.build();
    const toysSection: HTMLElement = this.toysSection.build();
    this.updateToys();
    toysMain.append(this.valueFilterSection.sectionHTML, this.rangeFilterSection.sectionHTML, sortSection, toysSection);

    document.body.append(header, toysMain, footer);
    window.onbeforeunload = () => localStorage.setItem('userToysSettings', JSON.stringify(this.toysSettings));
  }
  updateChoosedIdList() {
    this.choosedToysDisplay.textContent = this.selectedToysCount;
  }
  updateToys() {
    this.toysSection.update(this.toysSettings, this.searchValue);
  }
  resetSettings() {
    Object.keys(this.toysSettings.valueFilters.choosedForms).forEach((key) => {
      this.toysSettings.valueFilters.choosedForms[key as keyof FormsList] = false;
    });
    Object.keys(this.toysSettings.valueFilters.choosedColors).forEach((key) => {
      this.toysSettings.valueFilters.choosedColors[key as keyof ColorList] = false;
    });
    Object.keys(this.toysSettings.valueFilters.choosedSizes).forEach((key) => {
      this.toysSettings.valueFilters.choosedSizes[key as keyof Sizes] = false;
    });
    this.toysSettings.valueFilters.onlyFavorites = false;
    this.toysSettings.rangeFilters.countRange = [0, 12];
    this.toysSettings.rangeFilters.yearRange = [1940, 2020];
    this.valueFilterSection.update();
    this.rangeFilterSection.update();
    this.updateToys();
  }
}
