import { ToysPage } from './components/toysPage';
import { AppInterface, ToyItem, ToysPageInterface, ToysSettings } from './interfaces/interfaces';
import defaultSettings from './defaultSettings';

export class App implements AppInterface {
  toys: ToysPageInterface;

  constructor(toysList: Array<ToyItem>) {
    const toysData: ToysSettings = JSON.parse(localStorage.getItem('userToysSettings')) || defaultSettings;
    this.toys = new ToysPage(toysList, toysData);
  }
  init() {
    this.toys.draw();
  }
}
