import { PageBuilder } from './pageBuilder';
import { ToyItem, ToysSettings } from '../interfaces/interfaces';
import { CardDescriptionEnum, ColorsEnum, findValByKey, ShapesEnum, SizesEnum } from '../interfaces/supportConstants';
import { ValueItems } from '../interfaces/types';

export class ToysSection extends PageBuilder {
  toysList: Array<ToyItem>;
  toysContainer: HTMLElement;
  updateIdListTrigger: void;
  toysIdList: Array<Number>;
  triggerUpdateIdList: () => void;
  constructor(toysList: Array<ToyItem>, toysIdList: Array<number>, triggerUpdateIdList: () => void) {
    super();
    this.toysList = toysList;
    this.toysContainer = this.setElement({ tag: 'SECTION', classList: ['toys'] });
    this.toysIdList = toysIdList;
    this.triggerUpdateIdList = triggerUpdateIdList;
  }
  update(settings: ToysSettings, inputValue: string): void {
    const checkPropList = (props: ValueItems<boolean>): boolean => Boolean(Object.values(props).includes(true));
    const descriptionPairs: Array<[string, string]> = Object.entries(CardDescriptionEnum);
    let filteredToysList: Array<ToyItem> = [...this.toysList];

    filteredToysList = filteredToysList.filter((toyItem) => {
      if (checkPropList(settings.valueFilters.choosedForms)) {
        const filteredPairs: Array<[string, boolean]> = Object.entries(settings.valueFilters.choosedForms).filter(
          (pair) => pair[1]
        );
        const toyShape: string = findValByKey(Object.entries(ShapesEnum), toyItem.shape);
        return Boolean(filteredPairs.find((pair) => pair[0] === toyShape));
      }
      return true;
    });
    filteredToysList = filteredToysList.filter((toyItem) => {
      if (checkPropList(settings.valueFilters.choosedColors)) {
        const filteredPairs: Array<[string, boolean]> = Object.entries(settings.valueFilters.choosedColors).filter(
          (pair) => pair[1]
        );
        const toyColor: string = findValByKey(Object.entries(ColorsEnum), toyItem.color);
        return Boolean(filteredPairs.find((pair) => pair[0] === toyColor));
      }
      return true;
    });
    filteredToysList = filteredToysList.filter((toyItem) => {
      if (checkPropList(settings.valueFilters.choosedSizes)) {
        const filteredPairs: Array<[string, boolean]> = Object.entries(settings.valueFilters.choosedSizes).filter(
          (pair) => pair[1]
        );
        const toySize: string = findValByKey(Object.entries(SizesEnum), toyItem.size);
        return Boolean(filteredPairs.find((pair) => pair[0] === toySize));
      }
      return true;
    });
    if (settings.valueFilters.onlyFavorites) {
      filteredToysList = filteredToysList.filter((toyItem) => toyItem.favorite);
    }

    filteredToysList = filteredToysList.filter((toyItem) => {
      const countGroup: Array<number> = [
        settings.rangeFilters.countRange[0],
        +toyItem.count,
        settings.rangeFilters.countRange[1],
      ];
      const yearGroup: Array<number> = [
        settings.rangeFilters.yearRange[0],
        +toyItem.year,
        settings.rangeFilters.yearRange[1],
      ];
      return [countGroup, yearGroup].every((group: Array<number>) =>
        group.every((value: number, i: number) => (i < group.length - 1 ? value <= group[i + 1] : true))
      );
    });

    filteredToysList.sort((toyA, toyB) => {
      switch (settings.choosedSortKey) {
        case 'sortByNameUp':
          return toyA.name < toyB.name ? -1 : 1;
        case 'sortByNameDown':
          return toyA.name > toyB.name ? -1 : 1;
        case 'sortByYearUp':
          return Number(toyA.year) - Number(toyB.year);
        case 'sortByYearDown':
          return Number(toyB.year) - Number(toyA.year);
      }
    });

    filteredToysList = filteredToysList.filter((toyItem) => {
      const substr: string = inputValue.toUpperCase();
      const name: string = toyItem.name.toUpperCase();
      return name.includes(substr);
    });

    const toysItems: Array<HTMLElement> = filteredToysList.map((toyData) => {
      const currentSrc: string = `https://raw.githubusercontent.com/cptShell/christmas-data/main/assets/toys/${toyData.num}.avif`;
      const toyItem: HTMLElement = this.setElement({ tag: 'DIV', classList: ['toy'] });
      if (this.toysIdList.includes(Number(toyData.num))) toyItem.classList.add('active');
      toyItem.onclick = () => {
        const toyId = Number(toyData.num);
        if (!this.toysIdList.includes(toyId)) {
          this.toysIdList.push(toyId);
        } else {
          const idIndex = this.toysIdList.indexOf(toyId);
          this.toysIdList.splice(idIndex, 1);
        }
        toyItem.classList.toggle('active');
        this.triggerUpdateIdList();
      };
      const toyTitle: HTMLElement = this.setElement({
        tag: 'H3',
        classList: ['toy-title'],
        innerText: toyData.name,
      });
      const toyContent: HTMLElement = this.setElement({ tag: 'DIV', classList: ['toy-content'] });
      const imgContainer: HTMLElement = this.setElement({ tag: 'DIV', classList: ['img-container'] });
      const toyImage: HTMLElement = this.setElement({
        tag: 'IMG',
        classList: ['toy-image'],
        attributes: [
          { name: 'src', value: currentSrc },
          { name: 'alt', value: toyData.name },
        ],
      });
      const toyFavorite: HTMLElement = this.setElement({ tag: 'BUTTON', classList: ['toy-favorite'] });
      const descriptionList: HTMLElement = this.setElement({ tag: 'UL', classList: ['description-list'] });
      const descriptionItems: Array<HTMLElement> = new Array(descriptionPairs.length).fill(0).map((_, index) => {
        const currentPropKey: string = descriptionPairs[index][0];
        const currentValue: string | number | boolean = toyData[currentPropKey as keyof ToyItem];
        const descriptionText: string =
          descriptionPairs[index][1] +
          ': ' +
          (typeof currentValue !== 'boolean' ? String(currentValue) : currentValue ? 'да' : 'нет');
        const descriptionItem: HTMLElement = this.setElement({
          tag: 'LI',
          classList: ['description-item'],
          innerText: descriptionText,
        });
        return descriptionItem;
      });
      toyItem.append(toyTitle, toyContent);
      toyContent.append(imgContainer, descriptionList);
      imgContainer.append(toyImage, toyFavorite);
      descriptionList.append(...descriptionItems);
      return toyItem;
    });
    this.toysContainer.innerHTML = '';
    this.toysContainer.append(...toysItems);
  }
  build(): HTMLElement {
    const toysSection: HTMLElement = this.setElement({ tag: 'SECTION', classList: ['toys-container'] });
    toysSection.append(this.toysContainer);
    return toysSection;
  }
}
