import { RangeFilterData } from '../interfaces/types';
import { PageBuilder } from './pageBuilder';

export class RangeFilterSection extends PageBuilder {
  parentSettings: RangeFilterData;
  updateTrigger: () => void;
  sectionHTML: HTMLElement;
  constructor(parentSettings: RangeFilterData, updateTrigger: () => void) {
    super();
    this.parentSettings = parentSettings;
    this.updateTrigger = updateTrigger;
    this.sectionHTML = this.setElement({ tag: 'SECTION', classList: ['range-section'] });
  }

  update(): void {
    const rangeTitle: HTMLElement = this.setElement({
      tag: 'H2',
      classList: ['range-title'],
      innerText: 'фильтры по диапазону',
    });
    const rangeContainer: HTMLElement = this.setElement({ tag: 'DIV', classList: ['range-container'] });
    const countContainer: HTMLElement = this.setElement({ tag: 'DIV', classList: ['count-container'] });
    const countSubtitle: HTMLElement = this.setElement({
      tag: 'H3',
      classList: ['count-subtitle'],
      innerText: 'Количество экземпляров',
    });
    const countLabel: HTMLElement = this.setElement({ tag: 'DIV', classList: ['count-label'] });
    const countMin: HTMLElement = this.setElement({ tag: 'SPAN', classList: ['count-min'], innerText: String(1) });
    const countInput: HTMLElement = this.setElement({
      tag: 'INPUT',
      classList: ['count-input'],
      attributes: [
        { name: 'type', value: 'range' },
        { name: 'min', value: String(1) },
        { name: 'max', value: String(12) },
        { name: 'step', value: String(1) },
        { name: 'value', value: String(this.parentSettings.countRange[1]) },
      ],
    });
    countInput.onchange = (e: Event) => {
      this.parentSettings.countRange[1] = Number((<HTMLInputElement>e.target).value);
      this.updateTrigger();
    };
    const countMax: HTMLElement = this.setElement({ tag: 'SPAN', classList: ['count-max'], innerText: String(12) });
    countContainer.append(countSubtitle, countLabel);
    countLabel.append(countMin, countInput, countMax);
    const yearContainer: HTMLElement = this.setElement({ tag: 'DIV', classList: ['year-container'] });
    const yearSubtitle: HTMLElement = this.setElement({
      tag: 'H3',
      classList: ['year-subtitle'],
      innerText: 'Год приобретения:',
    });
    const yearLabel: HTMLElement = this.setElement({ tag: 'DIV', classList: ['year-label'] });
    const yearMin: HTMLElement = this.setElement({ tag: 'SPAN', classList: ['year-min'], innerText: String(1940) });
    const yearInput: HTMLElement = this.setElement({
      tag: 'INPUT',
      classList: ['year-input'],
      attributes: [
        { name: 'type', value: 'range' },
        { name: 'min', value: String(1940) },
        { name: 'max', value: String(2020) },
        { name: 'step', value: String(10) },
        { name: 'value', value: String(this.parentSettings.yearRange[1]) },
      ],
    });
    yearInput.onchange = (e: Event) => {
      this.parentSettings.yearRange[1] = Number((<HTMLInputElement>e.target).value);
      this.updateTrigger();
    };
    const yearMax: HTMLElement = this.setElement({ tag: 'SPAN', classList: ['year-max'], innerText: String(2020) });
    yearContainer.append(yearSubtitle, yearLabel);
    yearLabel.append(yearMin, yearInput, yearMax);
    rangeContainer.append(countContainer, yearContainer);
    this.sectionHTML.innerHTML = '';
    this.sectionHTML.append(rangeTitle, rangeContainer);
  }
}
